.main-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  min-height: 65px;
  background-color: #000000;
}
.main-nav__wrapper {
  width: 280px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
.main-nav__logo-link {
  display: flex;
  color: #283645;
  text-align: center;
  cursor: pointer;
}
.main-nav__logo-img {
  display: flex;
  width: 75px;
  height: 23px;
  margin-top: 23px;
}
.main-nav__switch {
  position: absolute;
  font-size: 0;
  top: 21px;
  right: calc(50% - 140px);
  background-image: url("../img/icon-menu-burger.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  margin: 0;
}
.main-nav__switch--hide {
  background-image: url("../img/icon-menu-burger.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  width: 50px;
  height: 24px;
  border: none;
}
.main-nav__switch--hide:active ~ .site-list--open {
  display: block;
}
.main-nav__switch--hide:active ~ .main-nav {
  margin-top: 255px;
}
.main-nav__switch--close {
  display: block;
  background-image: url("../img/icon-menu-cross.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  border: none;
}
.main-nav__list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  font-family: "Open Sans", "Arial", sans-serif;
  line-height: 18px;
  text-align: left;
  list-style: none;
  padding: 0;
  margin: 0;
}
.main-nav__list a {
  display: block;
  color: #ffffff;
  text-decoration: none;
  padding-top: 20px;
  padding-bottom: 20px;
}
.main-nav__switch--hide:active {
  display: block;
  background-image: url("../img/icon-menu-cross.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  border: none;
}
