

.comment-day {
  position: relative;
  width: 246px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  text-align: left;
  background-color: #f2f2f2;
}

.comment-day--tablet {
  position: relative;
  width: 565px;
  background-color: #f2f2f2;
}

.comment-day--desktop {
  position: relative;
  width: 886px;
  background-color: #f2f2f2;
}

.comment-day__crop-photo {
  width: 320px;
  height: 137px;
}

.comment-day__name {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 10px;
}

.comment-day__name span {
  font-size: 12px;
  line-height: 24px;
  font-weight: 300;
  text-transform: uppercase;
}

.comment-day__data {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 12px;
  line-height: 24px;
  font-weight: 300;
  text-transform: uppercase;
}

.comment-day__like-day {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.comment-day__like-day--desktop {
  display: flex;
  position: absolute;
  top: 31px;
  right: 30px;
  align-items: center;
}

.comment-day__comment-text {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  color: #283645;
  text-align: left;
  margin: 0;
}
